import Vue from 'vue';
import MaterialItem from '@/views/material-mall/components/MaterialItem.vue';
import { stop } from '_/jsx-utils';
import { i18n } from '@/i18n';

export default Vue.extend({
  props: {
    materialInfo: Object,
  },
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  render(h) {
    return (
      <MaterialItem ref='item' materialInfo={this.materialInfo}
      onpreview={() => this.$emit('preview')}>
        <el-tooltip effect="dark" placement="top" content={ i18n.t('material-mall.material_swatch') }>
          <div class="flex-1 flex-center" style="height:100%"
            onClick={ stop(() => this.$emit('color-family', this.materialInfo)) }>
            <i class="iconfont iconseka"></i>
          </div>
        </el-tooltip>
          {/* <i class='iconfont iconseka'
          onclick={stop(() => this.$emit('color-family', this.materialInfo))}></i> */}
          {/* <i class='iconfont iconyitusoutu'
          onclick={() => this.$emit('similarity', this.materialInfo)}></i> */}
      </MaterialItem>
    );
  },
});
