


















































import { defineComponent, ref, computed } from '@vue/composition-api';
import { Route } from 'vue-router';
import open from '@/utils/open-window';
import { getCurrentCompany } from '@/loaders/company';

function isQt(route: Route): string {
  if (navigator.userAgent.includes('Qt') || route.path.startsWith('/qt/')) {
    return '/qt';
  }

  return '';
}


export default defineComponent({
  name: 'MaterialItem',
  props: {
    materialInfo: {
      type: Object,
      default: () => ({}),
    },
    displayMode: {
      type: String as () => 'drape' | 'flat',
      default: 'drape',
    },
  },
  setup(props, { emit, root }) {
    const imgSrc = computed(() => {
      if (props.displayMode === 'flat') {
        return props.materialInfo.imgJson.texture ?? props.materialInfo.imgJson.flat;
      }
      return props.materialInfo.imgJson.thumbnail;
    });

    const company = computed(() => getCurrentCompany());

    function viewDetail() {
      if (company.value!.type === 3) {
        root.$router.push({
          path: `${isQt(root.$route)}/supplier-backend/preview/goods-detail`,
          query: {
            ...root.$route.query,
            id: props.materialInfo.id,
          },
        });
        return;
      }
      if (root.$route.path.startsWith('/qt')) {
        root.$router.push({
          path: '/qt/material-mall/goods-detail',
          query: {
            id: props.materialInfo.id,
          },
        });
      }
      open({
        path: '/material-mall/goods-detail',
        query: {
          id: props.materialInfo.id,
        },
      });
    }

    function preview() {
      emit('preview');
    }

    function goToCompany() {
      root.$router.push({
        path: `${isQt(root.$route)}/supplier-page/preview/homepage`,
        query: {
          companyId: props.materialInfo.companyId,
        },
      });
    }

    function goToColorBoard() {
      root.$router.push({
        path: `${isQt(root.$route)}/material-mall/colorboard`,
        query: {
          supplierArticleName: props.materialInfo.supplierArticleName,
        },
      });
    }
    function goToSearchSimilar() {
      const searchSimilarPath = `${isQt(root.$route)}/material-mall/searchsimilar`;
      const query = {
        colorFamily: props.materialInfo.colorFamily,
        type: props.materialInfo.materialType,
        id: props.materialInfo.id,
      };
      if (root.$route.path === searchSimilarPath) {
        root.$router.replace({
          path: searchSimilarPath,
          query,
        });
      } else {
        root.$router.push({
          path: searchSimilarPath,
          query,
        });
      }
    }

    return {
      imgSrc,

      viewDetail,
      preview,
      goToCompany,
      goToColorBoard,
      goToSearchSimilar,
    };
  },
});
